.under-development-panel {
  width: 100%;
  height: calc(100vh - 120px);
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .panel-content {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;

    .development-message {
      text-align: center;
      max-width: 400px;

      .icon {
        font-size: 64px;
        margin-bottom: 24px;
      }

      h3 {
        font-size: 28px;
        font-weight: 600;
        color: #333;
        margin-bottom: 16px;
      }

      p {
        color: #666;
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 24px;
      }

      .progress-info {
        padding: 16px 20px;
        background: #f8f9fa;
        border-radius: 8px;
        border-left: 4px solid #007bff;

        p {
          margin: 0;
          color: #495057;
          font-size: 14px;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .under-development-panel {
    .panel-content {
      padding: 24px 16px;

      .development-message {
        .icon {
          font-size: 48px;
          margin-bottom: 16px;
        }

        h3 {
          font-size: 24px;
          margin-bottom: 12px;
        }

        p {
          font-size: 14px;
          margin-bottom: 16px;
        }

        .progress-info {
          padding: 12px 16px;

          p {
            font-size: 13px;
          }
        }
      }
    }
  }
}
